@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

body {
    font-family: 'Poppins', sans-serif;
    max-width: 100%;
}

.head {
    text-align: center;
    font-size: 4rem;
}

.home h1 {
    font-size: 3srem;
}

.nav-item {
    padding: 10px 25px;
    text-align: center;
    font-size: 1.2rem;
    border-radius: 50px;
}

.nav-item:hover {
    transform: scale(1.2);
    transition: 0.3s;
}

nav a {
    text-decoration: none;
}

nav {
    border-bottom: 0.4px solid;
    margin-bottom: 25px;
}

.fa-adjust {
    font-size: 30px;
    padding: 10px;
    cursor: pointer;
}

.navbar-brand {
    font-size: 35px;
    text-align: center;
}

@media only screen and (max-width: 600px) {
    .navbar-brand {
        font-size: 30px;
    }
    
  }

  @media only screen and (max-width: 400px) {
    .navbar-brand {
        font-size: 25px;
    }
    
  }

